<!--
 * @Author: lbt666 '1319451895@qq.com'
 * @Date: 2022-08-08 11:38:39
 * @LastEditors: lbt666 '1319451895@qq.com'
 * @LastEditTime: 2022-10-24 11:53:17
 * @Description: 需求详情
-->
<template>
  <div class='main'
       v-cloak>
    <heads :bgColor="bgColor"
           :right="true"
           @onClickRight="$router.replace('market')">
      <div class="r">
        <img src="@/assets/home.png"
             alt="">
      </div>
    </heads>
    <div class="headBox"
         :style="{ background: [3, 4].includes(taskData.status) ? '#909CBA' : '#1F75D9' }">
      <div class="statusInfo"
           v-if="taskData">
        <div class="status">{{ $util.taskStatusTips(CurrentIdentity, taskData.my_status,
            taskData.status)
        }}</div>
        <div class="countDown">
          <template v-if="[0, 1].includes(taskData.status) && taskData.countDownTime > 0">
            <div class="times">{{ nowOrder[0].status === 1 ? '工期' : '报名' }} 还剩</div>
            <van-count-down :time="taskData.countDownTime"
                            format="DD天HH小时mm分ss秒" />
          </template>
          <template v-else>
            <div class="times">{{ $util.getUnixToDate('yyyy-mm-dd hh:mm:ss', taskData.update_ts) }}</div>
          </template>
        </div>
      </div>
      <div class="projectInfo">
        <div class="status"
             :class="taskData.my_bid_status === 2 ? 'alternativeStatus' : 'WinningStatus'"
             v-if="[0, 1].includes(taskData.my_status) && taskData.my_status !== 2">{{
              $util.CurrentUserStatus(taskData.my_bid_status)
          }}
        </div>
        <div class="callMe">
          <div class="InfoName">
            <div class="p_title">
              <div class="p_type"
                   v-if="taskData.type">{{ $util.taskType(taskData.type) }}</div>
              <div class="p_name">{{ taskData.title }}</div>
            </div>
            <div class="number">可报名人数{{ taskData.allows === -1 ? '不限' : `${taskData.allows || 0}人` }} · 在线联系</div>
          </div>
          <div class="contact"
               v-if="CurrentIdentity !== 'serve' && taskData.user_id !== userId"
               @click="contact(taskData)">
            <div class="contactImg">
              <img src="@/assets/contact.png"
                   alt="">
            </div>
            <div class="contactNmae">联系TA</div>
          </div>
          <div class="Deposit"
               v-if="CurrentIdentity === 'serve' && taskData.my_bid_status === 5 && nowOrder[1] && nowOrder[1].status === 0 && (nowOrder[1].end_time * 1000 - Date.now()) > 0"
               @click="Deposit">缴纳保证金
          </div>
        </div>
        <div class="price"
             :style="{ paddingBottom: taskData.status === 0 ? '15px' : '20px' }">
          <template v-if="taskData.amount === -1">
            <div class="p">面议</div>
          </template>
          <template v-else>
            <div class="p">{{ taskData.amount || 0 }}</div>
            <div class="unit">{{ taskData.amount_type === 1 ? 'SRS' : 'USDT' }}</div>
          </template>
        </div>
        <!-- <div class="errTips" v-if="taskData.status === 4 && taskData.user_id === userId">你的需求审核不通过，已被后台关闭</div> -->
        <div class="setting"
             v-if="taskData.user_id === userId">
          <template v-if="taskData.status === 0">
            <!-- 匹配中 -->
            <div class="item"
                 @click="edit(taskData.id)">
              <div class="icon">
                <img src="@/assets/edit.png"
                     alt="">
              </div>
              <div class="s_name">完善需求</div>
            </div>
            <div class="item"
                 @click="chengeTaskStatus(3, taskData.id)">
              <div class="icon">
                <img src="@/assets/pause.png"
                     alt="">
              </div>
              <div class="s_name">暂停需求</div>
            </div>
            <div class="item"
                 @click="chengeTaskStatus(4, taskData.id)">
              <div class="icon">
                <img src="@/assets/close.png"
                     alt="">
              </div>
              <div class="s_name">关闭需求</div>
            </div>
            <div class="item"
                 v-if="nowOrder[0] && taskData.win_user_id !== -1"
                 @click="payment(taskData)">
              <div class="icon">
                <img src="@/assets/metaphase.png"
                     alt="">
              </div>
              <div class="s_name">{{ nowOrder[0].tx_hash ? '已支付项目款' : '支付项目款' }}</div>
              <div class="s_name s_top s_samll"
                   v-if="nowOrder[0]">{{ $util.getUnixToDate('yyyy-mm-dd hh:mm',
                  nowOrder[0].pay_time)
              }}
              </div>
            </div>
          </template>
          <template v-else-if="taskData.status === 1">
            <!-- 服务中 -->
            <div class="item"
                 @click="chengeTaskStatus(4, taskData.id)">
              <div class="icon">
                <img src="@/assets/close.png"
                     alt="">
              </div>
              <div class="s_name">关闭需求</div>
            </div>
            <div class="item"
                 v-if="nowOrder[1] && nowOrder[1].status === 1">
              <div class="icon">
                <img src="@/assets/metaphase.png"
                     alt="">
              </div>
              <div class="s_name s_samll">服务方已缴纳保证金</div>
              <div class="s_name s_top"
                   v-if="nowOrder[1]">{{ $util.getUnixToDate('yyyy-mm-dd hh:mm',
                  nowOrder[1].pay_time)
              }}
              </div>
            </div>
            <div class="item"
                 @click="chengeTaskStatus(2, taskData.id)">
              <div class="icon">
                <img src="@/assets/complete.png"
                     alt="">
              </div>
              <div class="s_name">验收完成</div>
            </div>
          </template>
          <template v-else-if="taskData.status === 2">
            <!-- 已完成 -->
            <div class="item">
              <div class="icon">
                <img src="@/assets/metaphase.png"
                     alt="">
              </div>
              <!-- <div class="paymentTime">2020.12.12 12:21</div> -->
              <div class="s_name">已支付尾款</div>
            </div>
          </template>
          <template v-else-if="taskData.status === 3">
            <!-- 暂停需求 -->
            <!-- <div class="item" @click="edit()">
              <div class="icon">
                <img src="@/assets/again.png" alt="">
              </div>
              <div class="s_name">再次发布</div>
            </div> -->
            <div class="item"
                 @click="chengeTaskStatus(0, taskData.id)">
              <div class="icon">
                <img src="@/assets/open.png"
                     alt="">
              </div>
              <div class="s_name">公开需求</div>
            </div>
            <div class="item"
                 @click="edit(taskData.id)">
              <div class="icon">
                <img src="@/assets/perfect.png"
                     alt="">
              </div>
              <div class="s_name">完善需求</div>
            </div>
            <div class="item">
              <div class="icon">
                <img src="@/assets/delete.png"
                     alt="">
              </div>
              <div class="s_name">删除需求</div>
            </div>
          </template>
          <template v-else-if="taskData.status === 4">
            <!-- 关闭需求 -->
          </template>
          <template v-else>
          </template>
        </div>
        <div class="setting"
             v-if="CurrentIdentity === 'serve'">
          <!-- -1 待参与， 0=> 已参与， 1 =>参与中， 2=> 已完成 -->
          <template v-if="taskData.my_status === 0">
            <div class="item"
                 @click="CancelParticipation(taskData.bid_id, taskData.id)">
              <div class="icon">
                <img src="@/assets/close.png"
                     alt="">
              </div>
              <div class="s_name">取消参与</div>
            </div>
            <div class="item"
                 v-if="taskData.my_bid_status === 5 && nowOrder[1].status === 0">
              <div class="icon">
                <img src="@/assets/giveUp.png"
                     alt="">
              </div>
              <div class="s_name"
                   v-if="(nowOrder[1].end_time * 1000 - Date.now()) < 0">保证金缴纳过期</div>
              <div class="s_name"
                   v-else
                   @click="Deposit">缴纳保证金</div>
              <div class="s_name s_top s_samll"
                   v-if="nowOrder[1].end_time * 1000 - Date.now() > 0 && nowOrder[0].status === 1">
                <van-count-down :time="nowOrder[1].end_time * 1000 - Date.now()" />
              </div>
            </div>
            <div class="item"
                 v-if="taskData.my_bid_status === 5 && nowOrder[0].status === 1">
              <div class="icon">
                <img src="@/assets/metaphase.png"
                     alt="">
              </div>
              <div class="s_name">已支付项目款</div>
              <div class="s_name s_top s_samll"
                   v-if="nowOrder[0]">{{ $util.getUnixToDate('yyyy-mm-dd hh:mm',
                  nowOrder[0].pay_time)
              }}
              </div>
            </div>
            <div class="item"
                 @click="contact(taskData)">
              <div class="icon">
                <img src="@/assets/contact.png"
                     alt="">
              </div>
              <div class="s_name">联系他</div>
            </div>
          </template>
          <template v-else-if="taskData.my_status === 1">
            <div class="item"
                 @click="CancelParticipation(taskData.bid_id, taskData.id)">
              <div class="icon">
                <img src="@/assets/close.png"
                     alt="">
              </div>
              <div class="s_name">取消参与</div>
            </div>
            <div class="item"
                 v-if="taskData.my_bid_status === 5 && nowOrder[1] && nowOrder[1].status === 0">
              <div class="icon">
                <img src="@/assets/time.png"
                     alt="">
              </div>
              <div class="s_name">交保证金，开始服务</div>
              <div class="s_name s_top s_samll"
                   v-if="nowOrder[1]">
                <van-count-down :time="nowOrder[1].end_time * 1000 - Date.now()" />
              </div>
            </div>
            <div class="item"
                 v-else-if="taskData.my_bid_status === 6 && nowOrder[1] && nowOrder[1].status === 1">
              <div class="icon">
                <img src="@/assets/metaphase.png"
                     alt="">
              </div>
              <div class="s_name">已缴纳保证金</div>
              <div class="s_name s_top s_samll"
                   v-if="nowOrder[1]">{{ $util.getUnixToDate('yyyy-mm-dd hh:mm',
                  nowOrder[1].pay_time)
              }}
              </div>
            </div>
            <div class="item"
                 @click="contact(taskData)">
              <div class="icon">
                <img src="@/assets/contact.png"
                     alt="">
              </div>
              <div class="s_name">联系他</div>
            </div>
          </template>
          <template v-else-if="taskData.my_status === 2">
            <div class="item">
              <div class="icon">
                <img src="@/assets/metaphase.png"
                     alt="">
              </div>
              <div class="s_name">已收到尾款</div>
            </div>
            <div class="item"
                 @click="contact(taskData)">
              <div class="icon">
                <img src="@/assets/contact.png"
                     alt="">
              </div>
              <div class="s_name">联系他</div>
            </div>
          </template>
        </div>
      </div>
    </div>
    <div class="projectDetails">
      <van-tabs class="tabs"
                v-if="CurrentIdentity !== 'serve'"
                animated
                swipeable
                title-active-color="#1F75D9"
                title-inactive-color="#333"
                v-model="personnelActive"
                @change="personnelTabChange">
        <van-tab :title="`参与(${taskData.count || 0})`"
                 name="0"
                 @click="">
          <!-- 服务中和已完成没有切换 -->
          <div class="screen">
            <div class="screen_t">已参与人才</div>
            <div class="change">
              <div class="items"
                   :class="current0 === i ? 'active' : ''"
                   v-for="(el, i) in screenList"
                   :key="i"
                   @click="TalentScreening(i)">{{ el.name }}</div>
            </div>
          </div>
          <div class="empty"
               v-if="partakeList.length === 0">
            <div class="e_img">
              <img src="@/assets/emptyUser.png"
                   alt="">
            </div>
            <div class="e_tips">{{ errTips() }}</div>
            <div class="e_btn"
                 v-if="CurrentIdentity !== 'serve' && [2, 3].includes(taskData.status)">
              <van-button type="primary"
                          round
                          size="small"
                          block
                          color="#1F75D9"
                          @click="chengeTaskStatus(0, taskData.id)">
                {{ btnTips() }}
              </van-button>
            </div>
          </div>
          <div class="mains"
               v-for="(item, index) in partakeList"
               :key="index">
            <div class="personnelStatus alternativeStatus"
                 v-if="item.bid_status === 2">淘汰</div>
            <div class="personnelStatus eliminateStatus"
                 v-else-if="item.bid_status === 1">备选</div>
            <div class="personnelStatus WinningStatus"
                 v-else-if="item.bid_status === 5">中标</div>
            <div class="userInfo">
              <div class="userHead"
                   @click="seeUserPageHome(item)">
                <van-image :src="item.avatar_thumb">
                  <template v-slot:error><img :src="require('@/assets/user.png')"
                         alt=""></template>
                </van-image>
              </div>
              <div class="userBox">
                <div class="user">
                  <div class="username">{{ item.user_nickname }}</div>
                  <div class="userType">个人</div>
                </div>
                <div class="userIntroduction">自荐理由 {{ item.reason }}</div>
              </div>
            </div>
            <div class="operation"
                 v-if="taskData.user_id === userId && item.bid_status !== 5">
              <van-goods-action>
                <div class="iconItem">
                  <van-goods-action-icon text="淘汰"
                                         @click="modifyStatus(2, item)">
                    <template #icon>
                      <div class="acionIcon1">
                        <img src="@/assets/taotai.png"
                             alt="">
                      </div>
                    </template>
                  </van-goods-action-icon>
                  <van-goods-action-icon icon="cart-o"
                                         text="备选"
                                         @click="modifyStatus(1, item)">
                    <template #icon>
                      <div class="acionIcon2">
                        <img src="@/assets/beixuan.png"
                             alt="">
                      </div>
                    </template>
                  </van-goods-action-icon>
                </div>
                <div>
                  <van-goods-action-button type="danger"
                                           text="选TA中标"
                                           color="#68AEFF"
                                           v-if="zhongbiao.length === 0"
                                           :disabled="taskData.win_user_id > 0 ? true : false"
                                           @click="modifyStatus(5, item)" />
                  <van-goods-action-button type="warning"
                                           text="联系TA"
                                           color="#1F75D9"
                                           @click="contact(item)" />
                </div>
              </van-goods-action>
            </div>
          </div>
        </van-tab>
        <van-tab :title="`合作(${taskData.win_count || 0})`"
                 name="1"
                 @click="getTaskBiddersList(task_id, 5)">
          <div class="empty"
               v-if="zhongbiao.length === 0">
            <div class="e_img">
              <img src="@/assets/emptyUser.png"
                   alt="">
            </div>
            <div class="e_tips">{{ errTips() }}</div>
            <div class="e_btn"
                 v-if="CurrentIdentity !== 'serve' && [2, 3].includes(taskData.status)">
              <van-button type="primary"
                          round
                          size="small"
                          block
                          color="#1F75D9"
                          @click="chengeTaskStatus(0, taskData.id)">{{ btnTips() }}</van-button>
            </div>
          </div>
          <div class="mains mainsTop"
               v-for="(item, index) in zhongbiao"
               :key="index">
            <div class="userInfo">
              <div class="userHead">
                <van-image :src="item.avatar_thumb">
                  <template v-slot:error><img :src="require('@/assets/user.png')"
                         alt=""></template>
                </van-image>
              </div>
              <div class="userBox">
                <div class="user">
                  <div class="username">{{ item.user_nickname }}</div>
                  <div class="userType">个人</div>
                </div>
                <div class="userIntroduction">自荐理由 {{ item.reason }}</div>
              </div>
            </div>
            <div class="callMe"
                 :class="item.user_id !== userId && taskData.user_id !== userId ? 'btnStyle' : ''">
              <van-button type="primary"
                          v-if="CurrentIdentity === 'serve' && item.user_id === userId"
                          round
                          size="small"
                          plain
                          color="#1F75D9"
                          @click="payment(item)"
                          :disabled="![1].includes(taskData.status)">
                支付保证金
              </van-button>
              <van-button type="primary"
                          v-if="CurrentIdentity === 'employer' && taskData.user_id === userId && nowOrder[0].status
              === 0"
                          round
                          size="small"
                          plain
                          color="#1F75D9"
                          @click="payment(item)"
                          :disabled="![1].includes(taskData.status)">支付项目款
              </van-button>
              <van-button type="primary"
                          v-if="CurrentIdentity === 'employer' && taskData.user_id === userId"
                          round
                          size="small"
                          color="#1F75D9"
                          @click="contact(item)">联系TA
              </van-button>
            </div>
          </div>
        </van-tab>
      </van-tabs>
      <div class="projectContent">
        <div class="pd_title">
          <div>需求基本要求</div>
          <div class="edit"
               v-if="userId == taskData.user_id && [0, 3].includes(taskData.status)"
               @click="edit(taskData.id)">
            <img src="@/assets/edit.png"
                 alt="">
          </div>
        </div>
        <div class="cntent">
          <div class="pd_item">
            <div class="pd_label">需求标题</div>
            <div class="pd_tips">{{ taskData.title }}</div>
          </div>
          <div class="pd_item">
            <div class="pd_label">需求描述</div>
            <div class="pd_tips">{{ taskData.description }}</div>
          </div>
          <div class="pd_item">
            <div class="pd_label">需求类型</div>
            <div class="pd_tips">{{ $util.taskType(taskData.type) }}</div>
          </div>
          <div class="pd_item">
            <div class="pd_label">项目保证金</div>
            <div class="pd_tips">{{ taskData.bail || 0 }} {{ taskData.bail_type === 1 ? 'SRS' : 'USDT' }}</div>
          </div>
          <div class="pd_item">
            <div class="pd_label">项目付款方式</div>
            <div class="pd_tips">一次性付清</div>
          </div>
          <div class="pd_item">
            <div class="pd_label">项目款</div>
            <div class="pd_tips"
                 v-if="taskData.amount === -1">不限</div>
            <div class="pd_tips"
                 v-else>{{ taskData.amount || 0 }} {{ taskData.bail_type === 1 ? 'SRS' : 'USDT' }}</div>
          </div>
          <div class="pd_item">
            <div class="pd_label">允许报名名额</div>
            <div class="pd_tips">{{ taskData.allows === -1 ? '人数不限' : taskData.allows || 0 + '人' }}</div>
          </div>
          <div class="pd_item">
            <div class="pd_label">报名时间</div>
            <div class="pd_tips">{{ $util.getUnixToDate('yyyy-mm-dd', taskData.begin_time) }}</div>
          </div>
          <div class="pd_item">
            <div class="pd_label">交付时间</div>
            <div class="pd_tips">{{ $util.getUnixToDate('yyyy-mm-dd', taskData.delivery_time) }}</div>
          </div>
        </div>
      </div>
      <div class="projectContent">
        <div class="pd_title">需求动态</div>
        <div class="dynamic cntent">
          <van-steps direction="vertical"
                     :active="0"
                     active-color="#909CBA">
            <van-step v-for="(item, index) in logList "
                      :key="index"
                      v-if="index < dynamicNum">
              <div class="stepName">{{ $util.bidStatus(item.type) }}</div>
              <div class="stepTime">{{ $util.getUnixToDate('yyyy-mm-dd hh:mm:ss', item.create_ts) }}</div>
              <template #active-icon>
                <div class="s0">
                  <img src="@/assets/s0.png"
                       alt="">
                </div>
              </template>
              <template #inactive-icon>
                <div class="s1">
                  <img src="@/assets/s1.png"
                       alt="">
                </div>
              </template>
            </van-step>
          </van-steps>
          <div class="more"
               v-if="logList.length > 2">
            <div class="moreTips"
                 @click="seeMore">查看更多记录</div>
            <div class="moreIcon"
                 :style="{ transform: `rotate(${dynamicNum == 2 ? 0 : '180deg'})` }">
              <img src="@/assets/moreIcon.png"
                   alt="">
            </div>
          </div>
          <div class="pd_item">
            <div class="pd_label">需求编号</div>
            <div class="pd_tips"
                 v-if="taskData.serial_id">
              <div>{{ taskData.serial_id }}</div>
              <div class="box"
                   @click="$util.copy(taskData.serial_id)">
                <div class="copy">
                  <img src="@/assets/copy.png"
                       alt="">
                </div>
                <div class="copyTips">复制</div>
              </div>
            </div>
          </div>
          <div class="pd_item">
            <div class="pd_label">创建时间</div>
            <div class="pd_tips"> {{ $util.getUnixToDate('yyyy-mm-dd hh:mm:ss', taskData.create_ts) }}</div>
          </div>
        </div>
      </div>
      <div class="btns"
           v-if="CurrentIdentity === 'serve' && taskData.user_id !== userId && [-1, 3].includes(taskData.my_status) && taskData.status === 0 && taskData.countDownTime > 0">
        <van-button type="warning"
                    color="#909CBA"
                    plain
                    round
                    size="small"
                    @click="$router.go(-1)">不感兴趣</van-button>
        <van-button type="danger"
                    color="#1F75D9"
                    round
                    size="small"
                    disabled
                    v-if="taskData.begin_time < DateNow">
          报名结束
        </van-button>
        <van-button type="danger"
                    color="#1F75D9"
                    round
                    size="small"
                    v-else
                    @click="participate(taskData.id)">参与需求
        </van-button>
      </div>
    </div>
    <paymentLoading :showLoading="showLoading"
                    :countDown="countDown" />
  </div>
</template>
<script>
import { Toast, Dialog } from 'vant';
import paymentLoading from '@/components/paymentLoading'
import { taskDetails, taskBidders, taskBidderStatus, taskLogs, taskOrder, taskStatus, taskOrderStatus, taskBidderDel } from '@/api/request'
import BigToSmall from '@/util/BNUtils.js'
import { transferAddress } from '@/util/casinoContract.js'
import { ContactHim, ViewHomePage } from '@/util/bridge'

export default {
  components: {
    paymentLoading
  },
  data () {
    return {
      canyuPage: '',
      MAXORDERSTATUS: 30,
      showLoading: false,
      countDown: 300,
      bgColor: '#1F75D9',
      DateNow: new Date(new Date().toLocaleDateString()).getTime(),
      current0: 0,
      current1: 0,
      personnelActive: 1,
      dynamicNum: 2,
      task_id: '',
      taskData: '',
      userId: Number(localStorage.getItem('userId')),
      screenList: [
        {
          name: '全部',
          value: 0
        },
        {
          name: '备选',
          value: 0
        },
        {
          name: '淘汰',
          value: 0
        }
      ],
      partakeList: [],
      logList: [],
      zhongbiao: [],
      nowOrder: [{ end_time: 0, status: 0 }, { end_time: 0, status: 0 }]
    }
  },
  computed: {
    WEB3 () {
      return this.$store.state.WEB3
    },
    CurrentIdentity () {
      return this.$store.state.CurrentIdentity
    },
    SRSbalance () {
      return this.$store.state.SRSbalance
    },
    USDTbalance () {
      return this.$store.state.USDTbalance
    },
    SRScontract () {
      return this.$store.state.SRScontract
    },
    USDTcontract () {
      return this.$store.state.USDTcontract
    },
    transfer () {
      return this.$store.state.transfer
    },
    address () {
      return this.$store.state.address
    }
  },
  async mounted () {
    this.task_id = this.$route.query.id
    await this.getData(this.task_id)
    await this.getTaskBiddersList(this.task_id, 0)
    this.seeOrderStatus()
  },
  methods: {
    async contact (item) {
      let res = await ContactHim({
        userNiceName: item.user_nickname,
        userId: item.user_id,
        avatar: item.avatar_thumb
      })
      console.log(res)
    },
    async seeUserPageHome (item) {
      let res = await ViewHomePage({
        userNiceName: item.user_nickname,
        userId: item.user_id,
        avatar: item.avatar_thumb
      })
      console.log(res)
    },
    // 缴纳保证金
    Deposit () {
      console.log(this.nowOrder)
      if (this.nowOrder[0].status === 0) {
        Toast('请等待需求方缴纳完项目款后重试')
      } else {
        this.payment({
          task_id: this.task_id,
          user_id: this.userId
        })
      }
    },
    TalentScreening (i) {
      if (this.current0 === i) return
      this.current0 = i
      if (i === 0) {
        this.getTaskBiddersList(this.task_id, 0)
      } else if (i === 1) {
        // 备选
        this.getTaskBiddersList(this.task_id, 1)
      }
      else if (i === 2) {
        // 淘汰
        this.getTaskBiddersList(this.task_id, 2)
      }
    },
    DialogConfirm (parms, callback) {
      Dialog.confirm({
        title: parms.DialogTitle,
        message: parms.message,
        showCancelButton: true,
        confirmButtonText: parms.confirmButtonText,
        confirmButtonColor: '#1F75D9',
        cancelButtonText: parms.cancelButtonText
      })
        .then(() => {
          callback()
        })
        .catch(() => {
          // on cancel
        });
    },
    // 更新任务状态
    async chengeTaskStatus (status, task_id) {
      // 0 =>匹配中, 1 =>服务中, 2 => 已完成，3=>暂停需求，4=> 关闭需求
      let that = this
      if (this.taskData.status === status) return
      let parms = {
        DialogTitle: '',
        message: '',
        confirmButtonText: '',
        cancelButtonText: ''
      }

      switch (status) {
        case 0:
          parms.DialogTitle = '公开需求'
          parms.message = '是否公开需求让人才参与'
          parms.confirmButtonText = '确认公开'
          parms.cancelButtonText = '取消'
          break
        case 2:
          parms.DialogTitle = '提示'
          parms.message = '请确认项目已完成'
          parms.confirmButtonText = '确认完成'
          parms.cancelButtonText = '取消'
          break
        case 3:
          parms.DialogTitle = '暂停需求'
          parms.message = '暂停后将无法公开招募人才参与你的需求，暂停后可以再次公开需求。是否确认暂停需求'
          parms.confirmButtonText = '确认暂停'
          parms.cancelButtonText = '取消'
          break
        case 4:
          parms.DialogTitle = '关闭需求'
          parms.message = '关闭后将无法公开招募人才参与你的需求，关闭后不能再次公开需求是否确认关闭需求'
          parms.confirmButtonText = '确认关闭'
          parms.cancelButtonText = '取消'
          break
        default:
          parms.DialogTitle = '提示'
          parms.message = '请确认'
          parms.confirmButtonText = '确认'
          parms.cancelButtonText = '取消'
          break
      }

      this.DialogConfirm(parms, () => sendChangeStatus(status, task_id))
      async function sendChangeStatus (status, task_id) {
        await taskStatus({
          status: status,
          task_id: task_id
        })
        await that.getData(that.task_id)
      }
    },
    // 支付项目款
    async payment (item) {
      if (!this.SRSbalance) {
        Toast('支付失败')
        return
      }
      if (this.nowOrder[0].tx_hash) return;
      let AuthorizedAmount = 0
      let contract = null
      this.showLoading = true
      let timer = setInterval(() => {
        if (this.countDown > 0) {
          this.countDown--
          if (this.countDown == 0) {
            clearInterval(timer)
            this.showLoading = false
          }
        } else {
          clearInterval(timer)
        }
      }, 1000)
      if (this.taskData.amount_type === 1) {
        // srs
        AuthorizedAmount = this.SRSbalance
        contract = this.SRScontract
      } else if (this.taskData.amount_type === 2) {
        // utransfersdt
        AuthorizedAmount = this.USDTbalance
        contract = this.USDTcontract
      }
      console.log(AuthorizedAmount, contract, this.taskData.bail, this.$store.state)
      if (AuthorizedAmount === 0 || AuthorizedAmount < this.taskData.bail) {
        // 授权1亿
        this.obtainAuthorization(contract, timer, item)
      } else {
        this.transferPayment(timer, item)
      }
    },
    // 获取金额授权
    async obtainAuthorization (val, timer, item) {
      this.btnLoading = true
      let mount = BigToSmall.pow(100000000, 18)
      await val.methods
        .approve(transferAddress, mount)
        .send({
          from: this.address,
        })
        .then((res) => {
          console.log('res', res)
          if (this.taskData.amount_type === 1) {
            // srs
            this.$store.dispatch('ActionsSetSRSbalance', 100000000)
          } else if (this.taskData.amount_type === 2) {
            // utransfersdt
            this.$store.dispatch('ActionsSetUSDTbalance', 100000000)
          }
          this.transferPayment(timer, item)
        })
        .catch((err) => {
          Toast('取消授权')
          clearInterval(timer)
          this.showLoading = false
        })
    },
    // 调用转账支付合约
    async transferPayment (timer, item) {
      let that = this
      let res = await taskOrder({
        task_id: item.id ? item.id : item.task_id,
        user_id: item.user_id
      })
      // 1 需求方 2服务方
      let block = await that.WEB3.eth.getBlockNumber()
      let parms = res.data[that.CurrentIdentity === 'serve' ? 1 : 0]
      let sendNum = 0
      let sendTimer = null
      let mount = BigToSmall.pow(parms.amount, 18).toString()
      console.log('调用转账支付合约', parms.token_address, that.address, transferAddress, mount, parms.id.toString(), that.transfer)
      that.transfer.methods.transferToken(parms.token_address, that.address, transferAddress, mount, parms.id.toString()).send({
        from: that.address,
      }).then(async (res) => {
        console.log('交易信息', res)
        await taskOrderStatus({
          block_num: block,
          hx_hash: res.transactionHash,
          order_id: parms.id
        })

        sendTimer = setInterval(async () => {
          if (sendNum > that.MAXORDERSTATUS) {
            clearInterval(sendTimer)
            clearInterval(timer)
            that.showLoading = false
            Toast('支付失败')
            return
          } else {
            sendNum++
            // 查询订单状态
            let queryOrderStatus = await taskOrder({
              task_id: item.id ? item.id : item.task_id,
              user_id: item.user_id
            })
            if (queryOrderStatus.data[that.CurrentIdentity === 'serve' ? 1 : 0].status === 1) {
              clearInterval(sendTimer)
              clearInterval(timer)
              that.showLoading = false
              Toast('支付成功')
              await that.getData(that.task_id)
              await that.getTaskBiddersList(that.task_id, 0)
              that.seeOrderStatus()
            }
          }
        }, 3000)
      }).catch((err) => {
        console.log('transferPayment', err)
        Toast('支付失败')
        clearInterval(timer)
        that.showLoading = false
      })
    },

    // 参与、合作切换
    async personnelTabChange (val) {
      await this.getTaskBiddersList(this.task_id, val == 0 ? 0 : 5)
      await this.getData(this.task_id)
    },
    // 获取动态
    async dynamicLog (id) {
      let res = await taskLogs({
        task_id: id
      })
      this.logList = res.data
      console.log('获取动态', res)
    },
    // 修改状态
    async modifyStatus (bid_status, item) {
      //  1 => 备选，2 => 淘汰， 5=> 中标
      let that = this
      if (this.taskData.amount === -1) {
        Toast('请先完善需求中填写项目款')
        return
      }
      if (item.bid_status === 5) {
        Toast('用户已中标')
        return
      }
      if (item.bid_status === bid_status) return

      let parms = {
        DialogTitle: '',
        message: '',
        confirmButtonText: '',
        cancelButtonText: ''
      }

      switch (bid_status) {
        case 1:
          parms.DialogTitle = '提示'
          parms.message = '是否备选该用户'
          parms.confirmButtonText = '确认'
          parms.cancelButtonText = '取消'
          break
        case 2:
          parms.DialogTitle = '提示'
          parms.message = '您点击确认淘汰，服务方会收到系统通知的淘汰消息，该服务方会从您的参与列表中删除'
          parms.confirmButtonText = '确认淘汰'
          parms.cancelButtonText = '先不淘汰'
          break
        case 5:
          parms.DialogTitle = '确认合作'
          parms.message = '需要缴纳项目款到平台，直至您点击验收完成，该项目款再由平台扣除服务费（项目款的30%）之后支付给服务方，本次支付不可撤回，确认支付吗？'
          parms.confirmButtonText = '确认合作'
          parms.cancelButtonText = '取消'
          break
        default:
          parms.DialogTitle = '提示'
          parms.message = '请确认'
          parms.confirmButtonText = '确认'
          parms.cancelButtonText = '取消'
          break
      }

      this.DialogConfirm(parms, () => sendChangeStatus(bid_status, item))
      async function sendChangeStatus (bid_status, item) {
        await taskBidderStatus({
          bid_status: bid_status,
          id: item.id,
          task_id: item.task_id,
          user_id: item.user_id
        })
        await that.getTaskBiddersList(that.task_id, 0)
        await that.getTaskBiddersList(that.task_id, 5)
        await that.getData(that.task_id)
        that.seeOrderStatus()
      }

    },
    // 参与列表
    async getTaskBiddersList (id, bid_status) {
      // 加分页
      let res = await taskBidders({
        task_id: id,
        bid_status: bid_status,
        cursor: this.canyuPage
      })
      if (res.data.length < 10) {
        this.canyuPage = ''
        if (bid_status === 0) {
          this.partakeList = []
        } else {
          this.zhongbiao = []
        }
      } else {
        this.canyuPage = res.data[res.data.length - 1].create_ts
      }
      if (bid_status === 0) {
        this.partakeList = this.partakeList.concat(res.data)
      } else {
        this.zhongbiao = this.zhongbiao.concat(res.data)
      }
      console.log('列表', this.partakeList, this.zhongbiao)
    },
    // 修改需求
    edit (id) {
      localStorage.removeItem('form')
      this.$router.push({
        path: 'information',
        query: {
          id: id,
          status: 'edit'
        }
      })
    },
    async seeOrderStatus () {
      let parms = {
        task_id: this.task_id,
        user_id: this.userId
      }
      let res = await taskOrder(parms)
      if (res.data.length) {
        this.nowOrder = res.data
        console.log('当前订单状态', res.data)
      } else {
        console.error('当前订单支付状态异常v1/task/order' + JSON.stringify(parms))
      }
    },
    async getData (id) {
      let res = await taskDetails({
        task_id: id
      })
      if ((res.data.begin_time + 86400000) < this.DateNow) {
        res.data.my_status = 3
        res.data.status = 3
      } else {
        // res.data.countDownTime = (this.DateNow + 86400000) - (res.data.begin_time) - Date.now()
        if ((this.DateNow) === res.data.begin_time) {
          res.data.countDownTime = (this.DateNow + 86400000) - Date.now()
        } else {
          res.data.countDownTime = res.data.begin_time - Date.now()
        }
      }
      if ([3, 4].includes(res.data.status)) {
        this.bgColor = '#909CBA'
      } else {
        this.bgColor = '#1F75D9'
      }
      this.taskData = res.data
      await this.dynamicLog(id)
      console.log(res.data)
    },
    errTips () {
      let tips = ''
      switch (this.taskData.status) {
        case 3:
          tips = '需求暂未公开，暂时没有人才参与' // 暂停需求
          break;
        default:
          tips = '暂时没有人才' // 暂停需求
          break;
      }
      return tips
    },
    btnTips () {
      let tips = ''
      switch (this.taskData.status) {
        case 2:
          tips = '再次发布'
          break;
        case 3:
          tips = '公开需求'
          break;
      }
      return tips
    },
    // 参与需求
    participate (task_id) {
      this.$router.push({
        path: 'improveInformation',
        query: {
          task_id: task_id
        }
      })
    },
    // 取消参与
    async CancelParticipation (bid_id, task_id) {
      let that = this
      let parms = {
        DialogTitle: '取消参与',
        message: '是否取消参与',
        confirmButtonText: '确认取消',
        cancelButtonText: '取消'
      }

      this.DialogConfirm(parms, () => sendChangeStatus(bid_id, task_id))
      async function sendChangeStatus (bid_id, task_id) {
        await taskBidderDel({
          bid_id: bid_id,
          task_id: task_id
        })
        await that.getData(that.task_id)
      }
    },
    // 筛选
    screen (name, title) {
      console.log(name, title)
    },
    seeMore () {
      this.dynamicNum = this.dynamicNum === 9999 ? 2 : 9999
    }
  }
}
</script>
