      width: 118px;
      height: 104px;
      margin-bottom: 20px;
      margin-top: 26px;
    }

    .e_tips {
      font-size: 14px;
      line-height: 1;
      margin-bottom: 25px;
      font-weight: 400;
      color: #909cba;
    }

